
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import Countup from 'vux/src/components/countup/index.vue'

import equipmentData from '../../../components/equipment_data';
export default {
name:'TestManagerIndex',
components: {Marquee, MarqueeItem, Countup, equipmentData},

data(){
this.chartSettings = {
stack: { '用户': ['访问用户'] },
area: true,


}
this.chartExtend = {
itemGap:10,
series: {
smooth: false
},

};
return{
asyncCount: [],
operating:.5,
chartData: {
columns: ['日期', '访问用户'],
rows: [
{ '日期': '1月', '访问用户': 1393,'下单率': 0.32 },
{ '日期': '2月', '访问用户': 3530,'下单率': 0.26 },
{ '日期': '3月', '访问用户': 2923,'下单率': 0.76 },
{ '日期': '4月', '访问用户': 1723,'下单率': 0.49 },
{ '日期': '5月', '访问用户': 3792,'下单率': 0.323 },
{ '日期': '6月', '访问用户': 4593,'下单率': 0.78 }
]
},
isTestManager_systemNumber: false ,
TestManager_systemNumber: {}
}
},
computed:{

},
mounted(){
this.$emit('setIndexHeaderTitle',{flag:true,title: '您好，实验室经理!'});
this.getTestManager_systemNumber();
this.getSystem_info();
this.getTestManager_systemRate();
},
methods:{
goActivelyNumber(){
this.$router.push({name:'ActivelyNumber'})
},
goTestProject(){
this.$router.push({name:'TestProject'})
},
goTeststandArd(){
this.$router.push({name:'TeststandArd'})
},
goTestRecord(){
this.$router.push({name:'TestRecord'})
},
goTestEquipment(){
this.$router.push({name:'TestEquipment'})
},
goTestInventory(){
this.$router.push({name:'TestInventory'})
},
goTestReport(){
this.$router.push({name:'TestReport'})
},
goAlarmlog(){
this.$router.push({name:'Alarmlog'})
},
changeRotate(operating){
if(operating == 0){
return 'rotate(-140deg)'
}else if(operating == 1 ){
return 'rotate(38deg)'
}else{
return 'rotate('+ (1-operating-0.1 ) * -120 + 'deg)'
}
},
drawLine(ArrFrom){

let myChart = this.$echarts.init(document.getElementById('myChart'))

myChart.setOption({
xAxis: {
type: 'category',
boundaryGap: true,
data: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月','10月'],
axisLine:{

lineStyle:{
color:'#999',
}
},
axisTick:{
show:false
}
},
yAxis: {
type: 'value',
interval:20,
splitNumber : 5,
min:0,
axisLabel:{
show: true,
interval: 'auto',
formatter: '{value} %',
margin:1
},
axisLine:{
show: false,
lineStyle:{
color:'#999',
}
},
axisTick:{
show:false
},
},
grid:[{
left:35,
x:25,
y:40,
y2:40,
tooltip:{
show:true,
padding:1,
},
}],
title:{
text:'实验室设备保养率',
padding:0,
textStyle:{ fontFamily: 'simhei', fontSize: 20,  fontStyle: 'normal', fontWeight: '400',},
},
series: [{
data: ArrFrom,
type: 'line',
itemStyle:{
normal:{
color:"#00A5AF",
lineStyle:{
width:1,//折线宽度
color:"#7ed8d5"
}
},
},
smooth : false,
symbol: 'none',
areaStyle: {
normal: {
color: new this.$echarts.graphic.LinearGradient(
0, 0, 0, 1,
[
{offset: 0, color: '#dbfae9'},
{offset: 1, color: '#fff'}
]
)
}
}
}]
});
},
drawLineTwo(ArrFrom){

let myChart = this.$echarts.init(document.getElementById('myChartTwo'))

myChart.setOption({
xAxis: {
type: 'category',
boundaryGap: true,
data: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月','10月'],
axisLine:{
lineStyle:{
color:'#999',
}
},
axisTick:{
show:false
}
},
yAxis: {
type: 'value',
interval:20,
splitNumber : 5,
min:0,
axisLabel:{
show: true,
interval: 'auto',
formatter: '{value} %',
margin:1
},
axisLine:{
show: false,
lineStyle:{
color:'#999',
}
},
axisTick:{
show:false
},
},
grid:[{
left:35,
x:25,
y:40,
y2:40,
tooltip:{
show:true,
padding:1,
},
}],
title:{
text:'实验室设备维修率',
padding:0,
textStyle: {//主标题文本样式{"fontSize": 18,"fontWeight": "bolder","color": "#333"}
fontFamily: 'simhei',
fontSize: 20,
fontStyle: 'normal',
fontWeight: '400',
},
},
series: [{
data: ArrFrom,
type: 'line',
itemStyle:{
normal:{
color:"#00A5AF",
lineStyle:{
width:1,//折线宽度
color:"#7ed8d5"
}
},
},
smooth : false,
symbol: 'none',
areaStyle: {
normal: {
color: new this.$echarts.graphic.LinearGradient(
0, 0, 0, 1,
[
{offset: 0, color: '#dbfae9'},
{offset: 1, color: '#fff'}
]
)
}
}
}]
});
},
/**@name 设备维修保养率 */
getTestManager_systemRate(){
this.$vux.loading.show()
this.$http.get(this.$conf.env.getTestManager_systemRate).then( res =>{
var maintainList = [];var upkeepList = [];
for(var Element in res.data.maintain){
maintainList.push(this.toPoint(res.data.maintain[Element]))
}
for(var Element in res.data.upkeep){
upkeepList.push(this.toPoint(res.data.upkeep[Element]))
}
this.drawLine(maintainList);
this.drawLineTwo(upkeepList);
this.$vux.loading.hide()
}).catch(err =>{
console.log(err)
this.$vux.loading.hide()
this.$vux.toast.text(err.response?err.response.data:'服务器错误')
})
},
toPoint(data){
if(data){
return (data.replace("%", "")*1).toFixed(1);
}else{
return  0
}
},
/**@name 公告栏*/
getSystem_info(){
this.$http.get(this.$conf.env.getSystem_info).then( res =>{
this.asyncCount = res.data.results;
this.$vux.loading.hide()
}).catch(err =>{
this.$vux.loading.hide()
this.$vux.toast.text(err.response?err.response.data:'服务器错误')
})
},
getTestManager_systemNumber(){
this.$http.get(this.$conf.env.getTestManager_systemNumber).then( res =>{
this.TestManager_systemNumber = res.data;
this.isTestManager_systemNumber = true;
this.$vux.loading.hide()
}).catch(err =>{
this.$vux.loading.hide()
this.isTestManager_systemNumber = true;
this.TestManager_systemNumber = {"num1": 0,"num2": 0,"num3": 0}
this.$vux.toast.text(err.response?err.response.data:'服务器错误')
})
}
}
}



